<template>
    <div class="mine">
        <Tabs value="name1">
            <TabPane label="基本资料" name="name1">
                <ul class="ul">
                    <li>
                        <div class="left">管理员账号：</div>
                        <div class="right">
                            {{adminInfo.admin}}
                        </div>
                    </li>
                    <li>
                        <div class="left">管理员昵称：</div>
                        <div class="right">
                            <Input maxlength="20" v-model="username" placeholder="请输入管理员昵称"
                                   style="width: 30rem"/>
                        </div>
                    </li>
                    <li>
                        <div class="left">管理员性别：</div>
                        <div class="right">
                            <el-radio v-model="gender" :label="1">男</el-radio>
                            <el-radio v-model="gender" :label="0">女</el-radio>
                        </div>
                    </li>
                    <li>
                        <div class="left">角色：</div>
                        <div class="right">
                            {{adminInfo.role === 1 ? '超级管理员' : '管理员'}}
                        </div>
                    </li>
                    <li>
                        <div class="left"></div>
                        <div class="right">
                            <Button @click="saveAdmin" type="primary">保存信息</Button>
                        </div>
                    </li>
                </ul>
            </TabPane>
            <TabPane label="密码修改" name="name2">
                <ul class="ul">
                    <li>
                        <div class="left">旧密码：</div>
                        <div class="right">
                            <Input v-model="oldPassword" placeholder="请输入你的旧密码" type="password" style="width: 300px" />
                        </div>
                    </li>
                    <li>
                        <div class="left">新密码：</div>
                        <div class="right">
                            <Input v-model="newPassword" placeholder="请设置你的新密码" type="password" style="width: 300px" />
                        </div>
                    </li>
                    <li>
                        <div class="left"></div>
                        <div class="right">
                            <Button @click="revisePwd" type="primary">修改密码</Button>
                        </div>
                    </li>
                </ul>
            </TabPane>
        </Tabs>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import config from './../../../config'
    import {saveAdminInfo, revisePwd} from "../../../api";
    import {check_name} from "@/common/js/myFun";

    export default {
        name: "Mine",
        data(){
            return{
                username: '',
                gender: 1,
                oldPassword: '',
                newPassword: '',
            }
        },
        computed:{
            ...mapState(['adminInfo'])
        },
        mounted() {
            if(config.getCache('adminId')){
                let timer = null;
                timer = setInterval(()=>{
                    if(this.adminInfo.id){
                        this.username = this.adminInfo.username;
                        this.gender = Number(this.adminInfo.gender);
                        clearInterval(timer);
                    }
                },10);
            }else{
                this.$router.replace('/admin')
            }
        },
        methods:{
            async revisePwd(){
                let {oldPassword, newPassword} = this;
                if(!oldPassword || !newPassword){
                    return this.$Message.warning('请填写所有字段');
                }
                let data = await revisePwd({
                    oldPassword, newPassword, id: config.getCache('adminId')
                });
                if(data.code === 200){
                    this.$Message.success(data.data);
                    config.delCache('adminId');
                    this.$store.dispatch('addAdminInfo', {adminInfo: {}});
                    this.$router.replace('/admin/login');
                }else{
                    this.$Message.error(data.data);
                }
            },
            async saveAdmin() {
                let {username, gender} = this;
                if (!check_name(username)) {
                    return this.$Message.warning('昵称格式错误');
                }
                let data = await saveAdminInfo({
                  reviseGender: gender,
                  reviseUsername: username, id: config.getCache('adminId')
                });
                if (data.code === 200) {
                    this.$Message.success(data.data);
                    this.$store.dispatch('addAdminInfo', {adminInfo: data.result.adminInfo});
                } else {
                    this.$Message.error(data.data);
                }
            },
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
    .mine{
        padding: 2rem
    }
</style>
